html {
  height: 100%;
  box-sizing: border-box;
}

*, *:before, *:after {
  box-sizing: inherit;
}

body {
  font-family: "Open Sans", sans-serif;
  height: 100%;
}

p {
  color: #000000;
}

ul {
  list-style: none;
}

a {
  text-decoration: none;
  margin: 0;
}

img,
picture {
  width: 100%;
  display: block;
}

.subtitulo {
  font-size: 0.5em;
}

.btn--slim {
  font-size: 1rem !important;
}

.titulo {
  padding: 0.5rem;
  font-size: 2rem;
  border-radius: 0.1rem;
  background-color: #000080;
  color: #FFFFFF;
  font-weight: bold;
  text-align: center;
}

.resetBtn {
  border: none;
}

.contenedorPrincipal {
  background-color: blue;
  padding: 1rem;
  height: 100%;
  width: 100%;
  display: grid;
  align-items: center;
}
.contenedorPrincipal .row .contenedorClima {
  color: #FFFFFF;
  font-weight: bold;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  text-align: center;
}
.contenedorPrincipal .row .contenedorClima .contenedorClima__hora {
  font-size: 4rem;
  line-height: 4.5rem;
}
.contenedorPrincipal .row .contenedorClima .contenedorClima__clima {
  font-size: 2rem;
  line-height: 2rem;
}
.contenedorPrincipal .row .contenedorClima .contenedorClima__temperaturas {
  display: flex;
}
.contenedorPrincipal .row .contenedorClima svg {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  box-sizing: border-box;
  display: block;
  background-color: #0e4166;
  background-image: linear-gradient(to bottom, rgba(14, 65, 102, 0.86), #0e4166);
}
.contenedorPrincipal .row .contenedorVideo {
  object-fit: center;
}
.contenedorPrincipal .row .contenedorVideo .videoYoutube {
  width: 100%;
  height: 100%;
  border-radius: 1em;
}

.bg1 {
  background-color: green;
}

.bg2 {
  background-color: gray;
}

.recuadroPrincipal {
  min-height: 20rem;
  overflow: hidden;
}

.recuadroSecundario {
  aspect-ratio: 1/1;
  min-height: 8rem;
  height: auto;
}

.recuadroPrincipal, .recuadroSecundario {
  border-radius: 1rem;
  background-color: #000080;
}

.recuadroSecundario {
  display: flex;
  overflow: hidden;
  height: 100%;
  width: 100%;
}

.recuadroMusica {
  background-color: #1ED760;
}
.recuadroMusica a {
  display: flex;
  justify-content: center;
  align-items: center;
  height: 100%;
  width: 100%;
  color: #FFFFFF;
  font-size: 7rem;
}
@media (min-width: 768px) {
  .recuadroMusica a {
    font-size: 10rem;
  }
}

.recuadroNotas {
  display: grid;
  place-items: center;
  cursor: pointer;
}
.recuadroNotas button {
  height: 100%;
}

.recuadroDerecho {
  position: relative;
  height: 100%;
}
.recuadroDerecho .recuadroPrincipal__contenedorBtns {
  display: flex;
  justify-content: space-around;
  align-items: center;
  gap: 1rem;
  padding: 0 2rem;
  height: calc(100% - 64px);
  background-color: #FFFFFF;
}
.recuadroDerecho .recuadroPrincipal__contenedorBtns button {
  flex-wrap: wrap;
  font-weight: bold;
  font-size: 1.4rem;
}
@media (min-width: 1024px) {
  .recuadroDerecho .recuadroPrincipal__contenedorBtns button {
    font-size: 2rem;
  }
}

.contenedorReceta {
  display: flex;
  flex-direction: column;
}
.contenedorReceta .contenedor__ingredientes {
  display: flex;
}

.carousel-inner {
  height: 100%;
}

.carousel-item {
  height: 100%;
}
.carousel-item picture {
  height: 100%;
}
.carousel-item picture img {
  object-fit: cover;
  height: 100%;
}

.contenedorModalImg {
  height: auto;
  object-fit: contain;
}

.modalBodyImg {
  padding: 0 !important;
  width: 100%;
}